.inputFormContainer {
    position: relative;
}

.dateInput input {
    font-size: 16px;
}

.errorContainer {
    min-height: 20px;
    /* Reserve space for error message */
}

.errorMessage {
    color: red;
    animation: fadeIn 0.5s ease-in-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}