.navDaysContainer {
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 1000;
}

.toggleButton {
    margin-bottom: 10px;
}

.navDaysForm {
    position: absolute;
    /* top: 100%; */
    right: 0;
    background-color: rgb(234, 237, 245);
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
    transition: all 0.3s ease;
    opacity: 0;
    transform: translateY(-20px);
    width: 300px;
}

.navDaysForm.open {
    opacity: 1;
    transform: translateY(0);
    /* transform: translateX(0); */

}

.formContent {
    display: flex;
    flex-direction: column;
    gap: 20px;
}