.formContainer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    /* grid-template-rows: auto auto auto; */
    gap: 20px;
    /* padding: 30px; */
    position: fixed;
    left: 55%;
    top: 45%;
    transform: translate(-50%, -50%);
    z-index: 1000;
    background-color: rgba(245, 245, 245, 0.95);
    width: 90%;
    max-width: 1200px;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}



/* FormGridStyles.css */

.formContainer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
    padding: 20px;
    position: fixed;
    background-color: rgba(255, 255, 255, 0.98);
    border-radius: 12px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    width: 90%;
    max-width: 800px;
    max-height: 85vh;
    overflow-y: auto;
    z-index: 20000;
    animation: fadeIn 0.3s ease-out;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translate(-50%, -50%) scale(0.95);
    }

    to {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }
}

.formContainer::-webkit-scrollbar {
    width: 8px;
}

.formContainer::-webkit-scrollbar-track {
    background: #f1f1f1;
}

.formContainer::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 4px;
}

.formContainer::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.formContainer .MuiFormControl-root {
    margin-bottom: 10px;
}

.formContainer .MuiInputBase-root {
    font-size: 0.9rem;
}

.formContainer .MuiInputLabel-root {
    font-size: 0.9rem;
}

.guestInfoContainer {
    grid-column: span 2;
}

.roomTypeContainer,
.paymentMethodContainer {
    grid-column: span 1;
}

.commentsStyles {
    grid-column: span 3;
}

input[type="submit"] {
    grid-column: span 3;
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
}

input[type="submit"]:hover {
    background-color: #45a049;
}

.span-2 {
    grid-column: span 2;
}

.span-3 {
    grid-column: span 3;
}

.personalInput {
    width: 100%;
}

button[aria-label="Close form"] {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: #888;
}

button[aria-label="Close form"]:hover {
    color: #555;
}

@media screen and (max-width: 768px) {
    .formContainer {
        grid-template-columns: 1fr 1fr;
        width: 95%;
        padding: 15px;
        gap: 10px;
    }
}

@media screen and (max-width: 480px) {
    .formContainer {
        grid-template-columns: 1fr;
    }

    .guestInfoContainer,
    .roomTypeContainer,
    .paymentMethodContainer,
    .commentsStyles,
    input[type="submit"] {
        grid-column: span 1;
    }
}