/* FormGridStyles.css */

.formContainer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.98);
    border-radius: 12px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    width: 90%;
    max-width: 800px;
    max-height: 85vh;
    overflow-y: auto;
    z-index: 20000;
    animation: fadeIn 0.3s ease-out;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translate(-50%, -50%) scale(0.95);
    }

    to {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }
}

.formContainer::-webkit-scrollbar {
    width: 8px;
}

.formContainer::-webkit-scrollbar-track {
    background: #f1f1f1;
}

.formContainer::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 4px;
}

.formContainer::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.formContainer .MuiFormControl-root {
    margin-bottom: 10px;
}

.formContainer .MuiInputBase-root {
    font-size: 0.9rem;
}

.formContainer .MuiInputLabel-root {
    font-size: 0.9rem;
}

.guestInfoContainer {
    grid-column: span 2;
}

.roomTypeContainer,
.paymentMethodContainer {
    grid-column: span 1;
}

.commentsStyles {
    grid-column: 1 / 3;
    grid-row: 3;
    width: 100%;
}

textarea,
input[type="date"] {
    border: 1px solid #ccc;
    /* Define un borde gris claro */
    padding: 8px;
    /* Añade un poco de padding */
    width: 100%;
    /* Asegúrate de que ocupe el 100% del ancho del contenedor */
    box-sizing: border-box;
    /* Incluye el padding y el borde en el ancho total */
}

textarea:focus {
    border-color: #66afe9;
    /* Cambia el color del borde cuando el textarea está enfocado */
    outline: none;
    /* Elimina el borde de enfoque predeterminado */
}

.room-type {
    display: flex;
    /* flex-direction: column; */
    justify-content: space-around;
}

input[type="submit"] {
    grid-column: span 3;
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
}

input[type="submit"]:hover {
    background-color: #45a049;
}

.span-2 {
    grid-column: span 2;
}

.span-3 {
    grid-column: span 3;
}

.personalInput {
    width: 100%;
}

button[aria-label="Close form"] {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: #888;
}

button[aria-label="Close form"]:hover {
    color: #555;
}

.MuiPopover-root {
    z-index: 21000 !important;
}

.submit-button {
    background-color: #4CAF50;
    /* Green */
    border: none;
    color: white;
    padding: 15px 32px;
    align-items: center;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    width: 50%;
    height: 50%;

}

.submit-button:hover {
    background-color: #45a049;
    /* Darker green on hover */
}

.submit-button:active {
    background-color: #3e8e41;
    /* Even darker green when clicked */
}

.submit-button-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.recepcionista-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.input-container {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

@media screen and (max-width: 910px) {
    .formContainer {
        grid-template-columns: 1fr;
        width: 100%;
        max-width: 100%;
        padding: 15px;
        gap: 10px;
        height: 100%;
        max-height: 100%;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        transform: none;
        position: fixed;
        overflow-y: auto;
        overflow-x: hidden;
        box-sizing: border-box;
    }

    .formColumn {
        width: 100%;
    }

    .guestInfoContainer,
    .roomTypeContainer,
    .paymentMethodContainer,
    .commentsStyles,
    input[type="submit"] {
        grid-column: 1;
    }

    .selectButton {
        width: 100%;
    }

    .MuiMenu-paper {
        width: 90%;
        max-width: none;
    }

    .InputForm {
        margin-bottom: 10px;
    }

    button[aria-label="Close form"] {
        top: 5px;
        right: 5px;
    }

    .submitButton {
        margin-top: 20px;
        padding: 15px;
        font-size: 1.1rem;
    }

    .MuiInputBase-root {
        font-size: 16px;
        /* Prevent zoom on focus in iOS */
    }

    .MuiInputLabel-root {
        font-size: 14px;
    }

    /* Ajustar el tamaño del texto en los inputs para mejor legibilidad en móviles */
    input,
    textarea,
    select {
        font-size: 16px !important;
    }

    /* Mejorar la interactividad de los botones en móviles */
    button,
    .MuiButtonBase-root {
        min-height: 44px;
        padding: 10px 15px;
    }

    /* Asegurar que los menús desplegables sean fáciles de tocar */
    .MuiMenuItem-root {
        min-height: 48px;
    }

    /* Ajustar el espaciado vertical entre elementos del formulario */
    .MuiFormControl-root {
        margin-bottom: 15px;
    }
}

/* Ajustes adicionales para pantallas muy pequeñas */
@media screen and (max-width: 480px) {
    .formContainer {
        padding: 10px;
        top: 0;
        max-height: 100vh;
        border-radius: 0;
    }

    .submitButton {
        position: sticky;
        bottom: 10px;
        width: 100%;
    }
}

.formWrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    z-index: 19999;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width: 910px) {
    .formWrapper {
        align-items: flex-start;
    }
}